<template>
  <el-form
    class="form"
    :inline="true"
    v-loading="loading"
    @submit.native.prevent="onSubmit"
    :model="config"
    label-width="150px"
  >
    <el-row :gutter="24">
      <el-col>
        <el-form-item label="充值声音">
          <el-select
            v-model="config.invest_sound"
            clearable
            placeholder="请选择声音"
          >
            <el-option
              v-for="(item, key) in config.sound_list"
              :key="key"
              :label="item.sound"
              :value="item.sound"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="是否开启充值声音">
          <el-select
            size="small"
            v-model="config.is_open_invest"
            clearable
            placeholder="请选择"
          >
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>

      <el-col>
        <el-form-item label="充值声音">
          <el-select
            v-model="config.cost_sound"
            clearable
            placeholder="请选择声音"
          >
            <el-option
              v-for="(item, key) in config.sound_list"
              :key="key"
              :label="item.sound"
              :value="item.sound"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="是否开启提现声音">
          <el-select
            size="small"
            v-model="config.is_open_cost"
            clearable
            placeholder="请选择"
          >
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>

      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      loading: false,
      config: {
        sound_list: null,
        cost_sound: '',
        invest_sound: '',
        is_open_cost: '',
        is_open_invest: '',
      },
    }
  },
  mounted() {
    this.loading = true

    Fetch('/sys/config/info', {
      type: 'sound',
    })
      .then(r => {
        this.config.sound_list = r.data.sound_list
        this.config.cost_sound = r.data.cost_sound
        this.config.invest_sound = r.data.invest_sound
        this.config.is_open_cost = r.data.is_open_cost
        this.config.is_open_invest = r.data.is_open_invest
      })
      .catch(res => {
        Message.error(res.msg)
      })

    this.loading = false
  },
  methods: {
    onSubmit() {
      this.loading = true
      Fetch('/sys/config/update', {
        type: 'sound',
        invest_sound: this.config.invest_sound,
        cost_sound: this.config.cost_sound,
        is_open_invest: this.config.is_open_invest,
        is_open_cost: this.config.is_open_cost,
      })
        .then(() => {
          Message.success('更新成功')
        })
        .catch(r => {
          Message.error(r.msg)
        })

      this.loading = false
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
